*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Gotham Pro', Arial, Helvetica, sans-serif;
  background-color: #1e252d;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
  cursor: not-allowed;
}

.stop-scroll {
  overflow: hidden;
}

.hover-opacity {
  transition: opacity 0.25s;
}

.hover-opacity:hover, .hover-opacity:focus {
  opacity: 0.7;
}

main {
  opacity: 0;
  animation: fadeIn 0.5s forwards;
}

.site-container {
  min-width: 32rem;
  overflow: hidden;
}

.a-main {
  padding-bottom: 5rem;
}

@media (min-width: 1920px) {
  .a-main {
    padding-bottom: 0;
  }
}

.bg-stripe {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transform: skew(-50deg);
  background: linear-gradient(125.07deg, #f39200 -14.08%, #fe6400 39.63%);
  z-index: -1;
}

@media (min-width: 1024px) {
  .bg-stripe {
    left: 30vw;
    right: 30vw;
    transform: skew(-30deg);
  }
}

@media (min-width: 1280px) {
  .bg-stripe {
    left: 40vw;
    right: 20vw;
  }
}

.header {
  display: flex;
  padding: 2.5rem 2.5rem 0;
  align-items: center;
  justify-content: center;
  margin-bottom: 5rem;
}

@media (min-width: 768px) {
  .header {
    padding: 5rem 5rem 0;
  }
}

.header__logo {
  height: 6rem;
}

@media (min-width: 768px) {
  .header__logo {
    margin-right: 5rem;
  }
}

.header__logo img {
  height: 100%;
  object-fit: contain;
}

.header__title {
  display: none;
  align-items: center;
  width: 50rem;
  background-color: #ffffff;
  border-radius: 2.5rem;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
  padding: 1.5rem 3rem;
}

@media (min-width: 768px) {
  .header__title {
    display: flex;
  }
}

.header__title p {
  font-size: 1.2rem;
  margin-right: 2rem;
  width: 30rem;
}

.header__title span {
  font-size: 2.5rem;
  font-weight: 900;
  transition: color 0.25s;
}

.a-hero {
  position: relative;
  width: 90%;
  max-width: 32rem;
  background-color: #ffffff;
  border-radius: 3.9rem;
  box-shadow: 0px 4px 35px rgba(0, 0, 0, 0.2);
  margin: 0 auto 5rem;
  padding-bottom: 30rem;
  opacity: 0;
  animation: fadeInUp 0.75s forwards;
