@media (min-width: 768px) {
  .a-hero__links {
    width: 50%;
  }
}

@media (min-width: 1024px) {
  .a-hero__links {
    flex-direction: row;
    width: unset;
  }
  .a-hero__links .a-button {
    margin-bottom: 0;
    margin-right: 4.3rem;
  }
}

.a-hero__link {
  color: #1e252d;
  font-size: 1.6rem;
  font-weight: 700;
}

.a-hero__stripes {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 3.9rem;
  overflow: hidden;
  z-index: 1;
}

.a-hero__stripes::before, .a-hero__stripes::after {
  position: absolute;
  content: '';
  width: 6rem;
  height: 50rem;
  background: linear-gradient(126.99deg, #f39200 0.99%, #fe6400 42.95%);
  transform: skew(-50deg);
}

@media (min-width: 1024px) {
  .a-hero__stripes::before, .a-hero__stripes::after {
    transform: skew(-30deg);
    width: 10rem;
    height: 100rem;
  }
}

.a-hero__stripes::before {
  right: 20rem;
  bottom: -15rem;
}

@media (min-width: 1024px) {
  .a-hero__stripes::before {
    right: 10rem;
    bottom: 0;
  }
}

.a-hero__stripes::after {
  right: 0;
  bottom: -8rem;
}

@media (min-width: 1024px) {
  .a-hero__stripes::after {
    right: -10rem;
    bottom: 0;
  }
}

.a-hero__img {
  position: absolute;
  width: 21rem;
  right: -1rem;
  bottom: 0;
  z-index: 2;
}

@media (min-width: 1024px) {
  .a-hero__img {
    width: 40rem;
    right: -4rem;
  }
}

@media (min-width: 1280px) {
  .a-hero__img {
    width: 50rem;
    right: -7rem;
  }
}

.a-button {
  display: inline-block;
  width: 22.5rem;
  line-height: 6.2rem;
  font-size: 2rem;
  font-weight: 900;
  color: #1e252d;
  border: 2.8px solid #1e252d;
  border-radius: 3rem;
  text-align: center;
}

@media (min-width: 768px) {
  .a-button {
    transition-property: border-color, color, background-color;
    transition-duration: 0.25s;
  }
  .a-button:hover {
    border-color: #fe6400;
    color: #ffffff;
    background-color: #fe6400;
  }
}

.a-futures {
  width: 90%;
  max-width: 32rem;
  margin: 0 auto;
  opacity: 0;
  animation: fadeIn 0.25s 0.5s forwards;
}

@media (min-width: 768px) {
  .a-futures {
    max-width: 70rem;
  }
}

@media (min-width: 1280px) {
  .a-futures {
    display: flex;
    align-items: center;
    max-width: 100rem;
  }
}

.a-futures__header {
  font-size: 2.4rem;
  font-weight: 900;
  color: #ffffff;
  text-align: center;
  margin-bottom: 4rem;
}

@media (min-width: 1280px) {
  .a-futures__header {
    font-size: 2rem;
    line-height: 2.8rem;
    text-align: unset;
    padding-left: 2rem;
    margin-bottom: 0;
  }
}

.a-futures__list {
  display: flex;
  flex-direction: column;
}

.a-futures__list li {
  opacity: 0;
  animation: fadeInRight 0.25s forwards;
}

.a-futures__list li:not(:last-child) {
  margin-bottom: 1rem;
}

.a-futures__list li:nth-child(1) {
  animation-delay: 0.75s;
}

.a-futures__list li:nth-child(2) {
  animation-delay: 1s;
}

.a-futures__list li:nth-child(3) {
  animation-delay: 1.25s;
}

@media (min-width: 768px) {
  .a-futures__list li {
    width: 30%;
  }
  .a-futures__list li:not(:last-child) {
    margin-bottom: 0;
  }
}

@media (min-width: 768px) {
  .a-futures__list {
    flex-direction: row;
    justify-content: space-between;
  }
}

.a-card {
  position: relative;
  width: 100%;
  height: 10.5rem;
  display: flex;
  align-items: center;
  background-color: #1e252d;
  color: #ffffff;
  font-weight: 700;
  border-radius: 1.2rem;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  padding: 2.5rem 2rem;
}

.a-card--white {
  color: #1e252d;
  background-color: #ffffff;
}

.a-card--white .a-card__number {
  color: #f3f3f3;
}

.a-card--orange {
  background-color: #fe6400;
}

.a-card--orange .a-card__number {
  color: #ec5d00;
}

.a-card__text {
  font-size: 1.4rem;
  line-height: 2rem;
  z-index: 2;
}

.a-card__number {
  position: absolute;
  right: 2rem;
  font-size: 7.3rem;
  color: #111316;
  z-index: 1;
}
