.a-futures {
  width: 90%;
  max-width: 32rem;
  margin: 0 auto;
  opacity: 0;
  animation: fadeIn 0.25s 0.5s forwards;

  @include respond(tablet-768) {
    max-width: 70rem;
  }

  @include respond(desktop-1280) {
    display: flex;
    align-items: center;
    max-width: 100rem;
  }

  &__header {
    font-size: 2.4rem;
    font-weight: 900;
    color: $color-light;
    text-align: center;
    margin-bottom: 4rem;

    @include respond(desktop-1280) {
      font-size: 2rem;
      line-height: 2.8rem;
      text-align: unset;
      padding-left: 2rem;
      margin-bottom: 0;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;

    li {
      @include list-margin(1rem);
      opacity: 0;
      animation: fadeInRight 0.25s forwards;

      &:nth-child(1) {
        animation-delay: 0.75s;
      }
      &:nth-child(2) {
        animation-delay: 1s;
      }
      &:nth-child(3) {
        animation-delay: 1.25s;
      }

      @include respond(tablet-768) {
        @include list-margin(0);
        width: 30%;
      }
    }

    @include respond(tablet-768) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
}
