.a-button {
  display: inline-block;
  width: 22.5rem;
  line-height: 6.2rem;
  font-size: 2rem;
  font-weight: 900;
  color: $color-dark;
  border: 2.8px solid $color-dark;
  border-radius: 3rem;
  text-align: center;

  @include respond(tablet-768) {
    transition-property: border-color, color, background-color;
    transition-duration: 0.25s;

    &:hover {
      border-color: $color-orange;
      color: $color-light;
      background-color: $color-orange;
    }
  }
}
