@mixin font-face($font-family, $url, $weight) {
  @font-face {
    font-family: '#{$font-family}';
    src: url('#{$url}.woff') format('woff'),
      url('#{$url}.woff2') format('woff2');
    font-weight: #{$weight};
    font-display: swap;
    font-style: normal;
  }
}

// @include font-face('NotoSans Regular', '../fonts/notosans', 600);
@include font-face('Gotham Pro', '../fonts/GothamPro', 400);
@include font-face('Gotham Pro', '../fonts/GothamPro-Bold', 700);
@include font-face('Gotham Pro', '../fonts/GothamPro-Black', 900);
