@mixin respond($breakpoint) {
  @if $breakpoint == phone-360 {
    @media (min-width: 360px) {
      @content;
    }
  }

  @if $breakpoint == tablet-768 {
    @media (min-width: 768px) {
      @content;
    }
  }

  @if $breakpoint == tablet-1024 {
    @media (min-width: 1024px) {
      @content;
    }
  }

  @if $breakpoint == desktop-1280 {
    @media (min-width: 1280px) {
      @content;
    }
  }

  @if $breakpoint == desktop-1440 {
    @media (min-width: 1440px) {
      @content;
    }
  }

  @if $breakpoint == desktop-1920 {
    @media (min-width: 1920px) {
      @content;
    }
  }
}
