.bg-stripe {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transform: skew(-50deg);
  background: linear-gradient(125.07deg, #f39200 -14.08%, #fe6400 39.63%);
  z-index: -1;

  @include respond(tablet-1024) {
    left: 30vw;
    right: 30vw;
    transform: skew(-30deg);

  }

  @include respond(desktop-1280) {
    left: 40vw;
    right: 20vw;
  }
}
