*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Gotham Pro', Arial, Helvetica, sans-serif;
  background-color: $color-dark;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.disabled {
  opacity: 0.5;
  pointer-events: none;
  cursor: not-allowed;
}

.stop-scroll {
  overflow: hidden;
}

.hover-opacity {
  transition: opacity 0.25s;

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}

main {
  opacity: 0;
  animation: fadeIn 0.5s forwards;
}

.site-container {
  min-width: 32rem;
  overflow: hidden;
}

.a-main {
  padding-bottom: 5rem;

  @include respond(desktop-1920) {
    padding-bottom: 0;
  }
}