.header {
  display: flex;
  padding: $padding-mobile $padding-mobile 0;
  align-items: center;
  justify-content: center;
  margin-bottom: 5rem;

  @include respond(tablet-768) {
    padding: $padding-tablet $padding-tablet 0;
  }

  &__logo {
    height: 6rem;

    @include respond(tablet-768) {
      margin-right: 5rem;
    }

    img {
      height: 100%;
      object-fit: contain;
    }
  }

  &__title {
    display: none;
    align-items: center;
    width: 50rem;
    background-color: $color-light;
    border-radius: 2.5rem;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
    padding: 1.5rem 3rem;

    @include respond(tablet-768) {
      display: flex;
    }

    p {
      font-size: 1.2rem;
      margin-right: 2rem;
      width: 30rem;
    }

    span {
      font-size: 2.5rem;
      font-weight: 900;
      transition: color 0.25s;
    }
  }
}
