.a-card {
  position: relative;
  width: 100%;
  height: 10.5rem;
  display: flex;
  align-items: center;
  background-color: $color-dark;
  color: $color-light;
  font-weight: 700;
  border-radius: 1.2rem;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
  padding: 2.5rem 2rem;

  &--white {
    color: $color-dark;
    background-color: $color-light;

    .a-card__number {
      color: #f3f3f3;
    }
  }
  
  &--orange {
    background-color: $color-orange;

    .a-card__number {
      color: #ec5d00;
    }
  }

  &__text {
    font-size: 1.4rem;
    line-height: 2rem;
    z-index: 2;
  }

  &__number {
    position: absolute;
    right: 2rem;
    font-size: 7.3rem;
    color: #111316;
    z-index: 1;
  }
}
