.a-hero {
  position: relative;
  width: 90%;
  max-width: 32rem;
  background-color: $color-light;
  border-radius: 3.9rem;
  box-shadow: 0px 4px 35px rgba(0, 0, 0, 0.2);
  margin: 0 auto 5rem;
  padding-bottom: 30rem;
  opacity: 0;
  animation: fadeInUp 0.75s forwards;

  @include respond(tablet-768) {
    max-width: 70rem;
    padding-bottom: 8rem;
  }

  @include respond(tablet-1024) {
    max-width: 100rem;
    padding-bottom: 0;
  }

  @include respond(desktop-1280) {
    max-width: 110rem;
  }

  &__inner {
    position: relative;
    padding: 3rem 2rem 0;
    z-index: 2;

    @include respond(tablet-768) {
      padding: 5.7rem 7.3rem 0;
    }

    @include respond(tablet-1024) {
      max-width: 53rem;
      padding: 5.7rem 0 5.7rem 7.3rem;
    }

    @include respond(desktop-1280) {
      max-width: 62rem;
    }
  }

  &__header,
  &__subheader {
    font-weight: 900;
    text-align: center;

    @include respond(tablet-768) {
      text-align: unset;
    }
  }

  &__header {
    font-size: 3.6rem;
    margin-bottom: 1.5rem;

    @include respond(tablet-768) {
      font-size: 5rem;
      margin-bottom: 4.2rem;
    }

    @include respond(desktop-1280) {
      font-size: 7rem;
    }
  }

  &__subheader {
    font-size: 1.8rem;
    margin-bottom: 3rem;

    @include respond(tablet-768) {
      font-size: 2.4rem;
    }

    @include respond(desktop-1280) {
      font-size: 3rem;
    }
  }

  &__desc {
    font-size: 1.6rem;
    margin-bottom: 3rem;

    @include respond(tablet-768) {
      font-size: 2rem;
    }
  }

  &__lists {
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
    background-color: $color-gray;
    border-radius: 1.6rem;
    padding: 2rem 1rem;
    margin-bottom: 3rem;

    @include respond(tablet-768) {
      flex-direction: row;
      justify-content: space-between;
      padding: 2.5rem 1.5rem;
    }

    @include respond(desktop-1280) {
      margin-bottom: 3.5rem;
    }

    ul {
      &:first-child {
        margin-bottom: 3.5rem;

        @include respond(tablet-768) {
          margin-bottom: 0;
        }
      }

      li {
        line-height: 1.6rem;
        @include list-margin(1rem);
      }
    }
  }

  &__links {
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: center;

    .a-button {
      margin-bottom: 4rem;
    }

    @include respond(tablet-768) {
      width: 50%;
    }

    @include respond(tablet-1024) {
      flex-direction: row;
      width: unset;

      .a-button {
        margin-bottom: 0;
        margin-right: 4.3rem;
      }
    }
  }

  &__link {
    color: $color-dark;
    font-size: 1.6rem;
    font-weight: 700;
  }

  &__stripes {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-radius: 3.9rem;
    overflow: hidden;
    z-index: 1;

    &::before,
    &::after {
      position: absolute;
      content: '';
      width: 6rem;
      height: 50rem;
      background: linear-gradient(126.99deg, #f39200 0.99%, #fe6400 42.95%);
      transform: skew(-50deg);

      @include respond(tablet-1024) {
        transform: skew(-30deg);
        width: 10rem;
        height: 100rem;
      }
    }

    &::before {
      right: 20rem;
      bottom: -15rem;

      @include respond(tablet-1024) {
        right: 10rem;
        bottom: 0;
      }
    }

    &::after {
      right: 0;
      bottom: -8rem;

      @include respond(tablet-1024) {
        right: -10rem;
        bottom: 0;
      }
    }
  }

  &__img {
    position: absolute;
    width: 21rem;
    right: -1rem;
    bottom: 0;
    z-index: 2;

    @include respond(tablet-1024) {
      width: 40rem;
      right: -4rem;
    }

    @include respond(desktop-1280) {
      width: 50rem;
      right: -7rem;
    }
  }
}
